<template lang="jade">
  .group-page(style="width: 5.4rem; height: 6.8rem")
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .contract.scroll-content(:class="{ center: type === 0 }")

      div(v-if="stock" style="margin: .3rem" v-bind:class=" ['ds-icon-stock-' + STATUS[stock.isDone].class ] ")
        h2(style="color: #333; margin: .3rem 0; text-align: center") 
        p.item &nbsp;&nbsp;&nbsp;{{ stock.nickName }} 
        p.item {{  STATUS[stock.isDone].title }} 
        p.item {{ stock.times }}
        p.item {{ TIME[stock.shareCycle] }} 

        .item.text-danger(style="display: inline-block; margin: 0") {{ TYPE[stock.ruleType].title }} {{ stock.sales }}  {{ stock.bouns }} 
          // p.text-green(style="text-align: right; margin: .05rem 0")  100000 
          // p.text-green(style=" margin: .05rem 0") 

        .item.buttons(style="margin: .3rem 0" v-if=" !self && stock.isDone === 0  ")
          button.ds-button.large.bold.primary(@click="paid") 
          button.ds-button.large.bold.cancel(@click="paidOut")

        .item.buttons(style="margin: .3rem 0" v-if=" self && stock.isDone === 1  ")
          button.ds-button.large.bold.primary(@click="subCheckBonus") 


      
</template>

<script>
  import api from '../../http/api'
  export default {
    data () {
      return {
        // 
        self: true,
        // 
        STATUS: [
          {id: 0, title: '', class: 'waiting-pay'},
          {id: 1, title: '', class: 'wait'},
          {id: 2, title: '', class: 'paid'},
          {id: 3, title: '', class: 'paid-out'}
        ],
        // 
        TIME: ['', '', ''],
        // 
        TYPE: [
          {id: 1, title: ''},
          {id: 2, title: ''}
        ],
        stock: null
      }
    },
    watch: {
      // 
      '$route': 'openRoute'
    },
    mounted () {
      this.self = this.$route.query.self
      this.qryBonusById(this.$route.query.id || '')
    },
    methods: {
      back () {
        this.$modal.redpocket({
          target: this.$el,
          content: '<h2 class="title">jerry</h2><p class="content"><span class="amount">2236.00</span> </p>',
          btn: ['']
        })
        this.$modal.redpocket({
          target: this.$el,
          content: '<p class="ds-icon-success-large" style="min-height: 64px"></p><h2 class="title"></h2> ',
          btn: ['']
        })
        this.$modal.redpocket({
          target: this.$el,
          content: '<h2 class="title"></h2><p class="content" style="text-align: left;"></p>',
          btn: ['']
        })
        this.$modal.redpocket({
          target: this.$el,
          content: '<pstyle="min-height: 64px"></p><h2 class="title"></h2> ',
          btn: ['', '']
        })
      },
      openRoute ({query: {id, self}}) {
        this.self = self
        if (id) this.qryBonusById(id)
      },
      qryBonusById (id) {
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(api.qryBonusById, {
          bonusId: id
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.stock = data
            setTimeout(() => {
              loading.text = '!'
            }, 100)
          } else loading.text = '!'
        }, (rep) => {
          // error
          this.$message.error('')
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      },
      paid () {
        let modal = this.$modal.redpocket({
          target: this.$el,
          content: '<h2 class="title">j' + this.stock.nickName + '</h2><p class="content"><span class="amount">2236.00</span> </p>',
          btn: [''],
          O: this,
          ok () {
            if (modal.btn[0] === '') {
              modal.btn = ['']
              return false
            } else {
              this.paraentCheckBonus(1)
            }
          }
        })
      },
      paidOut () {
        let modal = this.$modal.redpocket({
          target: this.$el,
          content: '<h2 class="title"></h2><p class="content" style="text-align: left;"></p>',
          btn: [''],
          O: this,
          ok () {
            if (modal.btn[0] === '') {
              modal.content = '<p style="min-height: 64px"></p><h2 class="title"> </h2>'
              modal.btn = ['', '']
              return false
            } else {
              this.paraentCheckBonus(2)
            }
          }
        })
      },
      // 
      // http://192.168.169.44:9901/cagamesclient/team/contractBonus.do?method=paraentCheckBonus&bonusId=1&sendType=1
      paraentCheckBonus (type) {
        this.$http.get(api.paraentCheckBonus, {
          bonusId: this.stock.id,
          sendType: type
        }).then(({data}) => {
          if (data.success === 1) {
            if (type === 1) {
              this.$modal.redpocket({
                target: this.$el,
                content: '<p class="ds-icon-success-large" style="min-height: 64px"></p><h2 class="title"></h2> ',
                btn: ['']
              })
            }
            this.qryBonusById(this.$route.query.id || '')
          }
        }, (rep) => {
          // error
          this.$message.error('')
        })
      },
      subCheckBonus () {
        this.$http.get(api.subCheckBonus, {
          bonusId: this.stock.id
        }).then(({data}) => {
          if (data.success === 1) {
            this.$modal.success({
              target: this.$el,
              content: '',
              btn: ['']
            })
            this.qryBonusById(this.stock.id)
          }
        }, (rep) => {
          // error
          this.$message.error('')
        })
      }
      // http://192.168.169.44:9901/cagamesclient/team/contractBonus.do?method=subCheckBonus&bonusId=1
    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .contract
    top TH
    background #fff
    text-align left
    &.center:after
      content ''
      height 100%
      width 0
      vertical-align middle
      display inline-block
    .form
      padding PWX

  .item
    margin .24rem 0
    text-align left
    padding-left 20%
  

 
</style>


