<template lang="jade">
  .me-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .width-draw-info.scroll-content

      .bank-form(v-if="stepIndex === 0")
        .notice(style="margin-top: .2rem")
          span.title 
          p.content
            |  
            span.text-danger 5
            |   
            span.text-danger {{ times }} 0
            |  
            br
            span.text-danger 
            br
            |  
            span.text-danger 6
            |  

        .form
          .buttons(style="float: right; padding: .2rem 0")
            .ds-button.primary.large(@click="(type = 'bind') &&  stepIndex++") 
            button.ds-button.cancel.large(@click=" (type = 'lock') &&  stepIndex++" v-bind:disabled = "!myBanks[0]") 

        el-table.header-bold.margin(:data="myBanks" style="margin: .2rem")
          el-table-column(label="")
            template(scope="scope")
              .ds-icon-bank-card.static(v-bind:class=" [ scope.row.class ] " style="margin: 0")

          el-table-column(prop="cardNo" label="")

          el-table-column(label="")
            template(scope="scope")
              span {{ scope.row.addtime }}

          el-table-column(label="" min-width="60"  align="center")
            template(scope="scope")
              .ds-button.text-button.blue(@click="unbindCard(scope.row)") 


      
      


      .bank-form(v-if="stepIndex === 1 && type === 'bind' ")
        
        .notice(style="margin-top: .2rem")
          span.title 
          p.content
            | 1. 
            span.text-danger   
            | 
            br
            | 2. 
            span.text-danger   
            | 
            br
            | 3. 
            span.text-danger  5 
            | 
            span.text-danger  2 
            | 
            br
            | 4. 


        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333")  
          span.text-blue {{ me.name }}
          |  
          span.ds-button.text-button.blue(style="float: right" @click="bi > 0 ? bi-- : stepIndex--") {{ '<' }}
        StepTabs(type="text" v-bind:steps = "bs" v-bind:stepIndex="bi")
        
        hr(style="height: 0; border: 0; border-top: 1px solid #d4d4d4; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="bi === 0")
         
          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            el-select.width1-5rem(v-model="bank")
              el-option(v-for="(b, index) in BANKS" v-bind:label="b.text" v-bind:value="b")

          p.item 
            el-select.width1-5rem(v-model="province")
              el-option(v-for="(p, index) in PROVINCES" v-bind:label="p.title" v-bind:value="p")
          
          p.item 
            el-select.width1-5rem(v-model="city")
              el-option(v-for="(c, index) in province.subRegionList" v-bind:label="c.title" v-bind:value="c")
          
          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large(v-model="branchName")
            span(style="font-size: .12rem")  120

          p.item &nbsp;&nbsp;&nbsp;
            input.ds-input.large(v-model="name")
            span(style="font-size: .12rem")  120

          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large(v-model="cardNo")
            span(style="font-size: .12rem")  161819

          p.item 
            input.ds-input.large(v-model="cardNoAgain")
            span(style="font-size: .12rem")  

          .buttons(style="margin-left: 1rem; padding-top: .05rem")
            .ds-button.primary.large(@click="checkbindBankCard") 


        .form(style="margin: .2rem .4rem" v-if="bi === 1")

          .item(style="line-height: .5rem") &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            p.banks(style="color: #333")
              span.ds-icon-bank-card.static(v-bind:class=" [ bank.class ] ")

            p.item {{ province.title }}
            p.item {{ city.title }}
            p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ branchName }}
            p.item &nbsp;&nbsp;&nbsp;{{ name }}
            p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              span(style="color: #333") {{ cardNo }}
         
          .buttons(style="margin-left: .95rem; padding-top: .05rem")
            .ds-button.primary.large(@click="bindBankCard") 



      .bank-form(v-if="stepIndex === 1 && type === 'unbind' ")
        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333")  
          span.text-blue {{ me.name }} 
          | 
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<' }}
        StepTabs(type="text" v-bind:steps = "s" v-bind:stepIndex="i")
        
        hr(style="height: 0; border: 0; border-top: 1px solid #ccc; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="i === 0")
          .item(style="line-height: .5rem") 
            p.banks(style="color: #333")
              span.ds-icon-bank-card.static(v-bind:class=" [ bank.class ] ")
              | {{ bank.cardNo }}
          
          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large(v-model="cardNo")
            span(style="font-size: .12rem")  
          
          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large(v-model="name")
            span(style="font-size: .12rem")  

          .buttons(style="margin-left: 1.15rem; padding-top: .05rem")
            .ds-button.primary.large(@click="unbindBankCardCheck") 

        .form(style="margin: .2rem" v-if="i === 1")
          .item(style="line-height: .5rem") 
            p.banks(style="color: #333")
              span.ds-icon-bank-card.static(v-bind:class=" [ bank.class ] ")
          
          p.item &nbsp;{{ bank.cardNo }}
          p.item(style="margin: .2rem 0") &nbsp;{{ bank.addtime }}
          p.item &nbsp;
            input.ds-input.large(v-model="cpwd" type="password")
        
          .buttons(style="margin-left: 0.75rem; padding-top: .05rem")
            .ds-button.primary.large(@click="checkSecurityPwd") 


      .bank-form(v-if="stepIndex === 1 && type === 'lock' ")
        .notice(style="margin-top: .2rem")
          span.title 
          p.content
            | 1. 
            span.text-danger 
            br
            | 2. 
            br
            | 
            br
            
            span.text-danger 



        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333")  
          span.text-blue {{ me.name }}
          |   
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<' }}

        hr(style="height: 0; border: 0; border-top: 1px solid #ccc; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="i === 0")

          .item(style="line-height: .5rem") &nbsp;&nbsp;&nbsp;
            div.banks(style="color: #333; display: inline-block")
              div(v-for=" bank in myBanks ")
                .ds-icon-bank-card.static(v-bind:class=" [ bank.class ] ") 
                span(style="color: #333")  {{ bank.cardNo}}
          
          
          p.item  
            input.ds-input.large(v-model="cpwd" type="password")

          .buttons(style="margin-left: .75rem; padding-top: .05rem")
            .ds-button.primary.large(@click="checkSecurityPwd") 


</template>

<script>
import StepTabs from 'components/StepTabs'
import { BANKS } from '../../util/static'
import api from '../../http/api'
import store from '../../store'
import Validate from '../../util/Validate'
import { dateTimeFormat } from '../../util/Date'
export default {
  data () {
    return {
      me: store.state.user,
      avaibleBanks: [],
      // BANKS: BANKS,
      myBanks: [],
      PROVINCES: [],
      // 
      bank: {},
      // 
      province: {},
      // 
      city: {},
      // 
      name: '',
      // 
      cardNo: '',
      // 
      branchName: '',

      type: 'bind',
      stepIndex: 0,
      s: ['01 ', '02 ', '03 '],
      i: 0,

      bs: ['01 ', '02 ', '03 '],
      bi: 0
    }
  },
  watch: {
    cardNo () {
      this.cardNo = this.cardNo.trim()
    },
    cardNoAgain () {
      this.cardNoAgain = this.cardNoAgain.trim()
    },
    name () {
      this.name = this.name.trim()
    },
    branchName () {
      this.branchName = this.branchName.trim()
    }
  },
  computed: {
    BANKS () {
      return BANKS.filter(b => {
        return this.avaibleBanks.find(ab => ab.apiName === b.apiName)
      })
    },
    textMoney () {
      return this.money
    }
  },
  mounted () {
    // this.myBanks = [
    //   {id: 1, class: 'zggsh', cardNo: 112132484244, bindTime: '1990-02-02 12:56:05', text: ''},
    //   {id: 1, class: 'zggsh', cardNo: 112132484244, bindTime: '1990-02-02 12:56:05', text: ''}
    // ]
    this.getBankList(this.getUserBankCards)
    this.getProvices()
  },
  methods: {
    unbindCard (row) {
      this.$modal.warn({
        content: '',
        target: this.$el,
        btn: ['', ''],
        ok () {
          this.type = 'unbind'
          this.stepIndex++
          this.bank = row
        },
        O: this
      })
    },
    // unbindNow () {
    //   this.__loading({
    //     text: '...',
    //     target: this.$el
    //   }, 10000)
    //   setTimeout(() => {
    //     this.$modal.success({
    //       target: this.$el,
    //       content: '',
    //       close () {
    //         this.stepIndex = 0
    //         this.i = 0
    //       },
    //       O: this
    //     })
    //   }, 10000)
    // },
    lockNow () {
      let modal = this.$modal.warn({
        content: '',
        target: this.$el,
        btn: ['', ''],
        ok () {
          this.lockBankCard(modal)
          return false
        },
        O: this
      })
    },
    getUserBankCards () {
      this.$http.get(api.getUserBankCards).then(({data}) => {
        if (data.success === 1) {
          this.myBanks = data.userBankCards
          this.myBanks.forEach(c => {
            c.addtime = dateTimeFormat(c.addTime.time)
            c.cardNo = '*****' + c.cardNo.slice(-4)
            c.class = BANKS.find(b => b.apiName === c.apiName)['class']
          })
        }
      }).catch(rep => {
      })
    },
    getBankList (fn) {
      this.$http.get(api.getBankList).then(({data}) => {
        if (data.success === 1) {
          this.avaibleBanks = data.allBankData
          fn()
        }
      }).catch(rpe => {
      })
    },
    getProvices () {
      this.$http.get(api.getProvices).then(({data}) => {
        if (data.success === 1) {
          this.PROVINCES = data.allProvinces
        }
      }).catch(rpe => {
      })
    },
    checkbindBankCard () {
      if (!this.bank.apiName || !this.province.id || !this.city.id) return this.$message.error({target: this.$el, message: ''})
      if (this.branchName.length < 3 || !Validate.chinese(this.branchName)) return this.$message.error({target: this.$el, message: ''})
      if (!Validate.chineseName(this.name)) return this.$message.error({target: this.$el, message: ''})
      if (!Validate.bankcard(this.cardNo)) return this.$message.error({target: this.$el, message: ''})
      if (this.cardNo !== this.cardNoAgain) return this.$message.error({target: this.$el, message: ''})
      this.bi++
    },
    bindBankCard () {
      this.$http.post(api.bindBankCard, {realName: this.name, cardNo: this.cardNo, apiName: this.bank.apiName, provinceId: this.province.id, cityId: this.city.id, branch: this.branchName}).then(({data}) => {
        if (data.success === 1) {
          // this.$message.success({target: this.$el, message: ''})
          this.$modal.success({
            content: '',
            target: this.$el,
            close () {
              this.stepIndex = 0
              this.bi = 0
              this.i = 0
            },
            O: this
          })
          this.bi++
          this.getUserBankCards()
          this.clearBankCard()
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(({data}) => {
        this.$message.error({target: this.$el, message: data.msg || ''})
      })
    },
    clearBankCard () {
      this.bank = {}
      this.province = {}
      this.city = {}
      this.branchName = ''
      this.name = ''
      this.cardNo = ''
      this.cardNoAgain = ''
    },
    unbindBankCardCheck () {
      if (!Validate.bankcard(this.cardNo)) return this.$message.error({target: this.$el, message: ''})
      if (!Validate.chineseName(this.name)) return this.$message.error({target: this.$el, message: ''})
      this.$http.post(api.unbindBankCardCheck, {realName: this.name, cardNo: this.cardNo, entry: this.bank.entry}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ''})
          this.i++
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(({data}) => {
        this.$message.error({target: this.$el, message: data.msg || ''})
      })
    },
    unbindBankCard () {
      let loading = this.$loading({
        text: '...',
        target: this.$el
      }, 10000)
      this.$http.post(api.unbindBankCard, {entry: this.bank.entry}).then(({data}) => {
        if (data.success === 1) {
          loading && loading.close()
          this.$modal.success({
            target: this.$el,
            content: '',
            close () {
              this.stepIndex = 0
              this.bi = 0
              this.i = 0
            },
            O: this
          })
          this.getUserBankCards()
          this.clearBankCard()
          this.cpwd = ''
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(({data}) => {
        this.$message.error({target: this.$el, message: data.msg || ''})
      }).finally(rep => {
        setTimeout(() => {
          loading && loading.close()
        }, 1000)
      })
    },
    checkSecurityPwd (type) {
      this.$http.post(api.checkSecurityPwd, {password: this.cpwd}).then(({data}) => {
        if (data.success === 1) {
          if (this.type === 'unbind') this.unbindBankCard()
          else if (this.type === 'lock') this.lockNow()
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(({data}) => {
        this.$message.error({target: this.$el, message: data.msg || ''})
      })
    },
    lockBankCard (modal) {
      this.$http.get(api.lockBankCard).then(({data}) => {
        if (data.success === 1) {
          modal.type = 'success'
          modal.btn = []
          modal.content = ''
          modal.ok = null
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(rep => {
        this.$message.error({target: this.$el, message: ''})
      })
    }
    // 
    // // http://192.168.169.44:9901/cagamesclient/person/bankcard.do?method=getBankList
    // getBankList: api + 'person/bankcard.do?method=getBankList',
    // // 
    // // http://192.168.169.44:9901/cagamesclient/person/bankcard.do?method=getUserBankCards
    // getUserBankCards: api + 'person/bankcard.do?method=getUserBankCards',
    // // 
    // // http://192.168.169.44:9901/cagamesclient/person/bankcard.do?method=bindBankCard&provinceId=11&cityId=535&branch=&realName=&cardNo=62123456789012345&aliasName=abc&bankId=1
    // bindBankCard: api + 'person/bankcard.do?method=bindBankCard&provinceId=11&cityId=535&branch=&realName=&cardNo=62123456789012345&aliasName=abc&bankId=1',
    // // 
    // // http://192.168.169.44:9901/cagamesclient/person/bankcard.do?method=unbindBankCard&cardId=2
    // unbindBankCard: api + 'person/bankcard.do?method=unbindBankCard& =2',
    // // 
    // // http://192.168.169.44:9901/cagamesclient/person/bankcard.do?method=lockBankCard
    // lockBankCard: api + 'person/bankcard.do?method=lockBankCard',
  },
  components: {
    StepTabs
  }
}
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  
  table-row-heigh(.4rem)
    
  .scroll-content
    top TH
  .width-draw-info
    .cashpwd-form
      padding-top .1rem
  
  .notice
    font-size .12rem
    margin 0 .2rem
    padding PWX
    background-color #fffde8
    border 1px solid #d5d09b
    radius()
    .content
      display inline-block
      margin 0
      line-height .25rem
      vertical-align top
  .form
    margin 0 .4rem
    .amount
      font-family Roboto
      font-size 0.5rem
  .item
    margin .1rem 0
  .banks
    display inline-block
    margin 0
    max-width 90%
    vertical-align top
  .ds-radio-label
    padding 0 .1rem
  // .ds-icon-bank-card
  //   width 167px
  //   height 40px
  //   margin .05rem 
  //   radius()
  //   display inline-block

  //   vertical-align middle
  .buttons
    .ds-button:not(:first-child)
      margin-left .1rem
</style>


