<template lang="jade">
  .group-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .stock-list.scroll-content
      
      
      .form(v-if="stepIndex === 0 ")
        
        div(style="text-align: center")
          .ds-button-group
            .ds-button.x-small.text-button(:class=" { selected: type === 0 } " @click=" type = 0 " ) 
            .ds-button.x-small.text-button(:class=" { selected: type === 1 } " @click=" type = 1 " ) 

        label.item  
          el-date-picker(v-model="st" type="datetime" placeholder="")
          |   
          el-date-picker(v-model="et" type="datetime" placeholder="")
          
        label.item  &nbsp; 
          el-select(v-model="s")
            el-option(v-for="S in STATUS" v-bind:label="S.title" v-bind:value="S")
        | &nbsp;&nbsp;
        .ds-button.primary.large.bold(@click="contract") 

        el-table.header-bold.nopadding(:data="data" v-bind:row-class-name="tableRowClassName")

          el-table-column(prop="nickName" label="" width="150" v-if="type === 1")

          el-table-column(prop="expireTm" label="" width="150" )

          el-table-column(prop="beginTm" label="" width="150" )

          el-table-column(prop="status" label="" align="center" width="150")
            template(scope="scope")
              span(:class=" { 'text-danger': scope.row.stat > 1,  'text-blue': scope.row.stat === 0, 'text-green': scope.row.stat === 1} ") {{ STATUS[scope.row.stat].title }}

          el-table-column(label="" align="center")
            template(scope="scope")
              .ds-button.text-button.blue(style="padding: 0 .05rem" @click=" goContractDetail(scope.row.id) ") 
              .ds-button.text-button.blue(v-if=" type === 1 && scope.row.stat > 2" style="padding: 0 .05rem" @click="++stepIndex && (user = scope.row)") 
              .ds-button.text-button.blue(v-if=" type === 1 && scope.row.stat < 2" style="padding: 0 .05rem" @click="++stepIndex && (user = scope.row)") 
      

      div(v-if="stepIndex === 1 ")
        p.title(style="padding: .1rem 0 .1rem .2rem; color: #333")
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<' }}


        div(style="margin: 0 10% 0 25%; margin-top: .3rem; min-width: 6rem" v-bind:class="[ user.state ]")

          p.item.block
            span.text-danger *
            | &nbsp;&nbsp;&nbsp;&nbsp;
            span(style="color: #333") {{ user.nickName }}

          .item.block
            span.text-danger *
            | 
            div(style="display: inline-block")
              
              el-date-picker(v-model="st1" type="datetime" placeholder="")
              |   
              el-date-picker(v-model="et1" type="datetime" placeholder="")

          // p.item.block
          //   span.text-danger *
          //   | 
 
          //   label(style="color: #333; padding: 0 .1rem").ds-radio-label(@click=" AT = 0 " v-bind:class=" { active: AT === 0 } ")
          //     span.ds-radio.white
          //     | 
 
          //   label(style="color: #333; padding: 0 .1rem").ds-radio-label(@click=" AT = 1 " v-bind:class=" { active: AT === 1 } ")
          //     span.ds-radio.white
          //     | 
 
          //   label(style="color: #333; padding: 0 .1rem").ds-radio-label(@click=" AT = 2 " v-bind:class=" { active: AT === 2} ")
          //     span.ds-radio.white
          //     | 

          p.item.block(v-for=" (CR, i) in CRULES ")
            span.text-danger {{ i===0? '*': '&nbsp;'}}
            | {{ TYPE[CR.ruletype].title }} &nbsp;&nbsp;&nbsp;
            span(style="color: #333")  
            el-select(v-model="CR.ruletype" style="width: .7rem" placeholder="")
              el-option(v-for="R in TYPE" v-bind:label="R.title" v-bind:value="R.id")
            | &nbsp;&nbsp;
            el-input-number.text-danger.text-right(style="width: .5rem;" v-model="CR.sales")
            span(style="color: #333")   
            el-input-number.text-danger.text-right(style="width: .5rem;" v-model="CR.bounsRate")
            |  %

          .buttons.item.block(style="padding-left: .55rem" v-if="ruleLength < 11")
            .ds-button.x-small.text-button.el-icon-plus.blue(@click=" ruleLength++ ")
              span(style="color: #333") &nbsp;

            .ds-button.x-small.text-button.el-icon-minus.blue(@click=" ruleLength-- " v-if="ruleLength > 3 ")
              span(style="color: #333") &nbsp;

          .buttons.item.block(style="padding-left: .6rem")
            .ds-button.primary.bold(@click="createContract") 


        
</template>

<script>
  import api from '../../http/api'
  import { dateTimeFormat } from '../../util/Date'
  export default {
    data () {
      return {
        // 0 
        // 1 
        type: 0,
        st: '',
        et: '',
        STATUS: [
          {id: 0, title: ''},
          {id: 1, title: ''},
          {id: 2, title: '', reason: ''},
          {id: 3, title: '', reason: ''},
          {id: 4, title: '', reason: ''}
        ],
        s: {},
        data: [],

        stepIndex: 0,
        user: {},
        st1: '',
        et1: '',
        AT: 0,
        // 
        TYPE: [{id: 0, title: ''}, {id: 1, title: ''}],
        r: {id: 0, title: ''},
        RULES: [
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0},
          {title: '', ruletype: 0, sales: 0, bounsRate: 0}
        ],
        ruleLength: 3
      }
    },
    computed: {
      CRULES () {
        return this.RULES.slice(0, this.ruleLength)
      }
    },
    watch: {
      type () {
        this.contract()
      }
    },
    mounted () {
      this.contract()
    },
    methods: {
      goContractDetail (id) {
        this.$router.push({
          path: '/group/3-3-4',
          query: {id: id, self: !this.type}
        })
      },
      contract () {
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(this.type === 0 ? api.queryMyContract : api.mySubContract, {
          startDate: this.st ? dateTimeFormat(this.st.getTime()).replace(/[\s:-]*/g, '') : '',
          endDate: this.et ? dateTimeFormat(this.et.getTime()).replace(/[\s:-]*/g, '') : '',
          status: this.s.id || ''
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            data.mySubContract && data.mySubContract.forEach(c => {
              if (c.id <= 0) {
                c.stat = 2
                c.beginTm = '--'
                c.expireTm = '--'
              }
            })
            this.data = data.contractList || data.mySubContract
            setTimeout(() => {
              loading.text = '!'
            }, 100)
          } else loading.text = '!'
        }, (rep) => {
          // error
          this.$message.error('')
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      },
      createContract () {
        let data = this.CRULES.slice()
        data.forEach(c => {
          c.bounsRate /= 100
          delete c.title
        })
        this.$http.post(api.createContract, {
          beginTm: this.st1 ? dateTimeFormat(this.st1.getTime()).replace(/[\s:-]*/g, '') : '',
          expireTm: this.et1 ? dateTimeFormat(this.et1.getTime()).replace(/[\s:-]*/g, '') : '',
          userId: this.user.userId,
          // sharecycle: this.AT,
          bonusRuleList: JSON.stringify(data)
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.$modal.success({
              content: '',
              btn: [''],
              target: this.$el,
              close () {
                this.contract()
              },
              O: this
            })
          }
        }, (rep) => {
          // error
          this.$message.error('')
        })
      },
      qryContractById (contractId) {
        this.$http.post(api.qryContractById, {
        }).then(({data}) => {
          // success
          if (data.success === 1) {
          }
        }, (rep) => {
          // error
          this.$message.error('')
        })
      },
      qryBonusById (contractId) {
        this.$http.post(api.qryBonusById, {
        }).then(({data}) => {
          // success
          if (data.success === 1) {
          }
        }, (rep) => {
          // error
          this.$message.error('')
        })
      }
      // ID
      // http://192.168.169.44:9901/cagamesclient/team/contractBonus.do?method=qryContractById&contractId=12
      // qryContractById: api + 'team/contractBonus.do?method=qryContractById',
      // ID
      // http://192.168.169.44:9901/cagamesclient/team/contractBonus.do?method=qryBonusById&bonusId=1
      // qryBonusById: api + 'team/contractBonus.do?method=qryBonusById'
    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .stock-list
    top TH
    .form
      padding 0 PWX

  .item
    display inline-block
    margin 0 PW .1rem 0
    &.block
      display block
  
  .el-select
  .el-input-number 
    width 1rem

</style>


