<template lang="jade">
  .group-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .user-list.scroll-content

      // 
      .form(v-if="stepIndex === 0")
        label.item  
          input.ds-input.small(v-model="name")

        label.item  
          el-input-number(v-model="minMoney")
          |   
          el-input-number(v-model="maxMoney")

        // el-select(v-model="o" placeholder="")
        //   el-option(v-for="O in OL" v-bind:label="O.title" v-bind:value="O")
        // | &nbsp;&nbsp;
        // label.ds-checkbox-label(@click="btos = !btos" v-bind:class="{active: btos}")
        //   .ds-checkbox
        //   | 
 
        // label.item
        //   el-select(v-model="u")
        //     el-option(v-for="U in UL" v-bind:label="U.title" v-bind:value="U")

        label.item  
          el-input-number(v-model="minPoint")
          |   
          el-input-number(v-model="maxPoint")

        .buttons
          .ds-button.primary.large.bold(@click="searNow") 
          .ds-button.cancel.large.bold(@click="addUserNow") 

        p(style="margin: .3rem 0 .15rem 0")
          el-breadcrumb(separator=">")
            el-breadcrumb-item(v-for="(B, i) in BL" @click.native=" link(B, i) " ) {{ B.title }}

        el-table.header-bold.nopadding(:data="data" v-bind:default-sort = "{prop: 'nickName'}" @cell-click="cellClick")

          el-table-column(class-name="pointer text-blue" prop="userId" label="" width="50" align="left")


          el-table-column(prop="nickName" sortable label="" width="100")

          el-table-column(prop="teamBalance" sortable label="" width="120" align="right")

          el-table-column(prop="userPoint" sortable label="" align="right" width="100")

          el-table-column.pl1(class-name=" pl2 pr1" prop="registerTime" label="" width="160" align="left")

          el-table-column(label="" width="40" )
            template(scope="scope")
              span {{ scope.row.uploadlevel !== "0" ? '' : '' }}

          el-table-column.pl1(class-name=" pl1 " label="" width="120")
            template(scope="scope")
              span {{ scope.row.isAuto ? '' : '' }}

          el-table-column(label="" align="center")
            template(scope="scope")
              .ds-button.text-button.blue(v-if=" scope.row.uploadlevel !== '0' "  style="padding: 0 .05rem" @click=" (stepType = 'topUp') && ++stepIndex && (user = scope.row) ") 
              .ds-button.text-button.blue(style="padding: 0 .05rem" @click=" (stepType = 'point') && ++stepIndex && (user = scope.row) && showAdjustInfo()  ") 
              .ds-button.text-button.blue(style="padding: 0 .05rem" @click=" (stepType = 'open') && ++stepIndex && (user = scope.row) && showUserAddCount()  ") 
              .ds-button.text-button.blue(style="padding: 0 .05rem" @click=" (user = scope.row) && goBonus()  ") 
              .ds-button.text-button.blue(style="padding: 0 .05rem" @click=" scope.row.showTeanBalance = ! scope.row.showTeanBalance ") 
              div(v-if="scope.row.showTeanBalance") 
                span.text-danger {{ scope.row.teamBalance }}

      // 
      div(v-if="stepIndex === 1 && stepType === 'topUp' ")
        p.title(style="margin: .2rem 0 .2rem .2rem; color: #333")  
          span.text-blue {{ user.name }}
          |  
          span.ds-button.text-button.blue(style="float: right" @click="topUpIndex > 0 ? topUpIndex-- : stepIndex--") {{ '<' }} 

        p(style="padding-left: 30%; margin-top: .7rem" v-if="topUpIndex === 0") 
          input.ds-input.large(v-model="cpwd" type="password" @keyup.enter="checkSecurityPwd")
          <br>
          span.ds-button.primary.large.bold(style="margin-left: .7rem; margin-top: .2rem" @click="checkSecurityPwd") 

        p(style="padding-left: 30%; margin-top: .7rem" v-if="topUpIndex === 1") 
          input.ds-input.large(v-model="money") 
          span.text-money  {{ textMoney }}
          <br>
          span.ds-button.primary.large.bold(style="margin-left: .7rem; margin-top: .2rem" @click="topUpIndex++") 

        p(style="padding-left: 30%; margin-top: .7rem" v-if="topUpIndex === 2") 
          span.amount {{ money }}
          |    
          span.text-money  {{ textMoney }}
          <br>
          span.ds-button.primary.large.bold(style="margin-left: .85rem; margin-top: .15rem" @click="recharge") 

      // 
      div(v-if="stepIndex === 1 && stepType === 'point' ")

        p.title(style="margin: .2rem 0 .2rem .2rem; color: #333")  
          span.text-blue {{ user.nickName }}
          |  
          span.ds-button.text-button.blue(style="float: right" @click=" stepIndex-- ") {{ '<' }} 

        p(style="text-align: center; margin-top: .2rem") : 
          span.text-blue {{ user.userName }} 
          | &nbsp;&nbsp;&nbsp;&nbsp;: 
          span(style="color: #333") {{ user.nickName }} 
          | &nbsp;&nbsp;&nbsp;&nbsp;
          span.text-danger {{ user.userPoint }}
        div(style="text-align: center; margin-top: .1rem")
          .ds-button-group(style="margin: 0")
            .ds-button.text-button(:class=" { selected: pointType === 'up' } " @click=" pointType='up' ") 
            .ds-button.text-button(:class=" { selected: pointType === 'down' }" @click=" pointType='down'  ") 

        .notice(style="margin: .2rem" v-if=" pointType === 'up' ")
          span.title 
          p.content
            | 1 
            br
            | 2 
            br
            | 373.7 

        .notice(style="margin: .2rem" v-if=" pointType === 'down' ")
          p.content
            | 
            span.text-danger 
            | 

        div(style="padding: 0 .2rem")

          el-table.header-bold(:data="pointData[pointType]" v-bind:row-class-name="tableRowClassName" style="margin: .2rem")

            el-table-column(prop="level" label="" align="right" width="120")

            el-table-column(prop="prize" label="" width="120" align="right")

            el-table-column(prop="3Day" label="3" width="200" align="right" v-if="pointType === 'up' ")


            el-table-column(prop="7Day" label="7" width="200" align="right")

        hr(style="height: 0; border: 0; border-top: 1px solid #d4d4d4; margin:  .1rem")
      
        p(style="padding: .1rem .4rem" v-if=" pointType === 'up' ") 3
          span.text-danger {{ threeDaysAmount }}
        p(style="padding: .1rem .4rem") 7
          span.text-danger {{ sevenDaysAmount }}

        p(style="padding: .1rem .4rem" v-if="thirtyDaysAmount") 7
          span.text-danger {{ thirtyDaysAmount }}

        p(style="padding: .1rem .4rem") &nbsp;&nbsp;
          label(style="display: inline-block")
            span(style="margin: 0 .15rem " v-for="P in PS")
              span.text-blue [{{ P.point }}]:
              span.text-danger {{ P.n }}

        p(style="padding: 0rem .4rem") 
          span.amount {{ myPoint }}
          | &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{ pointType === 'up' ? '' : '' }}
          el-input-number( v-model="point" style="width: .5rem" v-bind:min="range[pointType].min"  v-bind:max="range[pointType].max")
          |  % 
          span.text-money ({{ range[pointType].min }}~{{ range[pointType].max }})

        p(style="padding: .1rem .4rem .1rem .2rem" v-if=" pointType==='up' ")
          label(style="color: #666").ds-radio-label(@click=" AT = 0 " v-bind:class=" { active: AT === 0 } ")
            span.ds-radio.white
            | 

          label(style="color: #666").ds-radio-label(@click=" AT = 1 " v-bind:class=" { active: AT === 1 } ")
            span.ds-radio.white
            | 
        div.buttons(style="padding: .1rem .4rem")
          .ds-button.primary.large.bold(@click="adjustPoint") {{ pointType === 'up' ? '' : '' }}

      
      // 
      div(v-if="stepIndex === 1 && stepType === 'open' ")
        p.title(style="margin: .2rem 0 .2rem .2rem; color: #333")  
            span.text-blue {{ user.nickName }}
            |  
            span.ds-button.text-button.blue(style="float: right" @click=" stepIndex-- ") {{ '<' }} 

          p(style="text-align: center; margin-top: .2rem") : 
            span.text-blue {{ user.name }} 
            | &nbsp;&nbsp;&nbsp;&nbsp;: 
            span(style="color: #333") {{ user.nickName }} 
            | &nbsp;&nbsp;&nbsp;&nbsp;
            span.text-danger {{ user.userPoint }}

        div(style="padding: 0 1rem")

          el-table.header-bold.margin(:data="openUserData" v-bind:row-class-name="tableRowClassName" style="margin: .2rem")

            el-table-column(prop="point" label="" align="center" width="120")

            el-table-column(prop="src" label="" width="150" align="right")

            el-table-column(prop="dest" label="" width="150" align="right" v-if="pointType === 'up' ")

            el-table-column(label="" width="150" align="right")
              template(scope="scope")
                el-input-number.center(v-model="scope.row.i" v-bind:min="0" v-bind:max="10")
                
  
        hr(style="height: 0; border: 0; border-top: 1px solid #d4d4d4; margin:  .1rem")
        div.buttons(style="padding: .1rem .4rem; text-align: center")
          .ds-button.primary.large.bold(@click="distriAddCount") 






      
</template>

<script>
  import { digitUppercase } from '../../util/Number'
  import api from '../../http/api'
  export default {
    data () {
      return {
        // me: store.state.user,
        myPoint: '',
        stepIndex: 0,
        // topUp, point
        stepType: 'topUp',
        topUpIndex: 0,
        pointType: 'up',
        name: '',
        minMoney: '',
        maxMoney: '',
        minPoint: '',
        maxPoint: '',
        // 
        // OL: [
        //   {id: 1, title: ''},
        //   {id: 2, title: ''},
        //   {id: 3, title: ''}
        // ],
        // o: {},
        // // 
        // UL: [
        //   {id: 1, title: ''},
        //   {id: 2, title: ''}
        // ],
        // u: {},
        // 
        // btos: false,
        // 
        BL: [
          {title: ''}
        ],
        data: [{}],
        // 
        user: { account: 'xxxx', name: 'xxxxx', point: 7.5 },
        cpwd: '',
        money: 0,

        // 
        PS: [],
        //  1 2
        AT: 0,
        pointData: {
          up: [],
          down: []
        },
        range: {
          up: {},
          down: {}
        },
        threeDaysAmount: 0,
        sevenDaysAmount: 0,
        thirtyDaysAmount: 0,
        openUserData: []
      }
    },
    computed: {
      textMoney () {
        return digitUppercase(this.money)
      }
    },
    mounted () {
      this.getUserList()
    },
    methods: {
      cellClick (row, column, cell, event) {
        if (column.property === 'userId') {
          this.BL.push({
            id: row.userId,
            title: row.nickName
          })
          this.getUserList()
        }
      },
      link (B, i) {
        console.log(B, i)
        if (i !== B.length - 1) {
          this.BL = this.BL.slice(0, i + 1)
          this.getUserList()
        }
      },
      searNow () {
        this.getUserList()
      },
      addUserNow () {
        this.$router.push('/group/3-2-1')
      },
      getUserList () {
        // http://192.168.169.44:9901/cagamesclient/team/useList.do?method=getUserList&userName=dd&minPoint=0&maxPoint=8&maxBalance=100000&minBalance=0&startRegistTime=20161101000000&endRegistTime=20161231000000
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(api.getUserList, {
          userId: this.BL[this.BL.length - 1].id,
          userName: this.name,
          minPoint: this.minPoint,
          maxPoint: this.maxPoint,
          minBalance: this.minMoney || '',
          maxBalance: this.maxMoney || ''
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            setTimeout(() => {
              loading.text = '!'
            }, 100)
            this.data = data.subUserInfo.map(o => {
              o.showTeanBalance = false
              return o
            })
          } else loading.text = '!'
        }, (rep) => {
          // error
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      },
      checkSecurityPwd () {
        this.$http.post(api.checkSecurityPwd, {password: this.cpwd}).then(({data}) => {
          if (data.success === 1) {
            this.topUpIndex++
            this.$message.success({target: this.$el, message: data.msg || ''})
          } else {
            this.$message.error({target: this.$el, message: data.msg || ''})
          }
        }).catch(rep => {
          this.$message.error({target: this.$el, message: ''})
        })
      },
      recharge () {
        // http://192.168.169.44:9901/cagamesclient/team/useList.do?method=recharge&destId=5&amount=100.5
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(api.recharge, {
          destId: this.user.userId,
          amount: this.money
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            setTimeout(() => {
              loading.text = '!'
              this.stepIndex = 0
              this.topUpIndex = 0
            }, 100)
          } else loading.text = data.msg || '!'
        }, (rep) => {
          // error
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      },
      // http://192.168.169.44:9901/cagamesclient/team/useList.do?method=showAdjustInfo&destUserId=5
      showAdjustInfo () {
        this.$http.get(api.showAdjustInfo, {
          destUserId: this.user.userId
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.myPoint = data.userpoint
            this.pointData = data.downPoint
            this.range.up = data.rang.up
            this.range.down = data.rang.down
            this.pointData.up = data.upPoint
            this.pointData.down = data.downPoint
            this.threeDaysAmount = data['3DaysAmount']
            this.sevenDaysAmount = data['7DaysAmount']
            this.thirtyDaysAmount = data['30DaysAmount']
            this.PS = data.addUsersKeys.map(k => {
              return (k = {
                point: k,
                n: data[k]
              })
            })
          }
        }, (rep) => {
          // error
        })
      },
      adjustPoint () {
        this.$http.get(api.adjustPoint, {
          destUserId: this.user.userId,
          adjustType: this.pointType === 'up' ? 0 : 1,
          increaseType: this.AT,
          point: this.point
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.$message.success('')
          } else this.$message.error(data.msg || '')
        }, (rep) => {
          // error
        })
      },
      showUserAddCount () {
        this.$http.post(api.showUserAddCount, {
          destUserId: this.user.userId
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.openUserData = data.addUsersKeys.map(k => {
              return (k = {
                point: k,
                dest: data[k].dest,
                src: data[k].src,
                i: 0
              })
            })
          } else this.$message.error(data.msg || '')
        }, (rep) => {
          // error
          this.$message.error('')
        })
      },
      distriAddCount () {
        let items = []
        this.openUserData.forEach(o => {
          items.push({
            point: o.point,
            n: o.i
          })
        })
        this.$http.post(api.distriAddCount, {
          destUserId: this.user.userId,
          items: JSON.stringify(items)
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.$message.success('')
          } else this.$message.error(data.msg || '')
        }, (rep) => {
          // error
          this.$message.error('')
        })
      },
      goBonus () {
        if (this.user.userId) {
          this.$router.push({
            path: '/me/2-3-1',
            query: {
              userId: this.user.userId
            }
          })
        }
      }
      // http://192.168.169.44:9901/cagamesclient/team/useList.do?method=distriUserAddCount&destUserId=5&levelVip=1&levelOne=1&levelTwo=1&levelThree1
      // 
      // http://192.168.169.44:9901/cagamesclient/team/useList.do?method=adjustPoint&destUserId=5&adjustType=0&increaseType=1&point=0.1
    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .user-list
    top TH
    .form
      padding PWX

  .item
    display inline-block
    margin 0 PW .1rem 0

    
  .el-select
  .el-input-number
    width 1.2rem
  .el-select
    position relative
    top .01rem
  .amount
    font-family Roboto
    font-size 0.72rem
    color #333
  .text-money
    color #999
  
  .notice
    font-size .12rem
    margin 0 .2rem
    padding PWX
    background-color #fffde8
    border 1px solid #d5d09b
    radius()
    .content
      display inline-block
      margin 0
      line-height .25rem
      vertical-align top
</style>


