<template lang="jade">
  .group-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .ad.scroll-content

      div

        .notice(style="margin: .2rem; margin-bottom: .1rem")
          span.title 
          p.content
            | 1. 
            br
            | &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            span 6.5% 
            |  
            span.text-blue 
            |  
            span.text-danger 0.5
            br
            | &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            span.text-blue 
             
            span.text-danger 6.0%    
            br
            | 2.  
            span.text-danger 6.5
            
            br
            | 3. 
        
        p(style="padding: 0rem 1.9rem") 
          span.amount {{ userPoint }}

        p(style="padding: .05rem 1.9rem")  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          el-select(v-model="p")
            el-option(v-for="P in PS" v-bind:label="P" v-bind:value="P")
       
        p(style="padding: .05rem 1.9rem")  &nbsp;
          a.text-blue(href="www.baidu.com" style="display: inline-block") {{ url }} 
           
        div.buttons(style="padding: .1rem 2.93rem")
          .ds-button.primary.large.bold(@click="setKeepPoint") 

        div(style="padding: .4rem 2.93rem")
          .QR.ds-icon-QR(style="height: 1.96rem; width: 1.4rem; text-align: center")
            p(style="color: #333; font-weight: bold; padding-top: 1.5rem;") 
        

      





      
</template>

<script>
  import api from '../../http/api'
  export default {
    data () {
      return {
        // 
        PS: [],
        p: '',
        url: '',
        userPoint: ''
      }
    },
    computed: {
    },
    mounted () {
      this.showSpreadLinks()
    },
    methods: {
      showSpreadLinks () {
        this.$http.get(api.showSpreadLinks).then(({data}) => {
          // success
          if (data.success === 1) {
            this.userPoint = data.userPoint
            this.url = data.url
            this.p = this.autoPoint
            for (let i = data.range.min; i <= data.range.max; i += 0.1) {
              this.PS.push(i.toFixed(1))
            }
          } else this.$message.error(data.msg || '')
        }, (rep) => {
          // error
          this.$message.error('')
        })
      },
      setKeepPoint () {
        this.$http.get(api.setKeepPoint, {keepPoint: this.p}).then(({data}) => {
          // success
          if (data.success === 1) {
            this.$message.success(data.msg || '')
            this.showSpreadLinks()
          } else this.$message.error(data.msg || '')
        }, (rep) => {
          // error
          this.$message.error('')
        })
      }
      // http://192.168.169.44:9901/cagamesclient/team/createAccount.do?method=setKeepPoint&keepPoint=0.1

    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .ad
    top TH
    .form
      padding PWX

    
  .el-select
  .el-input-number
    width 1rem
  .el-select
    position relative
    top .01rem
  .amount
    font-family Roboto
    font-size 0.72rem
    color #333

  
  .notice
    font-size .12rem
    margin 0 .2rem
    padding PWX
    background-color #fffde8
    border 1px solid #d5d09b
    radius()
    .content
      display inline-block
      margin 0
      line-height .25rem
      vertical-align top
</style>


