          el-table-column(prop="modes" label="" width="50" align="right")
            template(scope="scope")
                span {{ MODES[scope.row.modes] }}     

          el-table-column(prop="totalPrice" label="" width="80" align="right")

          el-table-column(prop="bonus" label="" width="80" align="right")

          el-table-column(label="" align="center")
            template(scope="scope")
              div(v-if="scope.row.stat === 0 ")
                .ds-button.text-button.blue(style="padding: 0 .05rem" @click="(row = scope.row) && (show = true) && (type = 1) ") 

      
      // 
      el-table.header-bold.nopadding(:data="data" v-bind:row-class-name="tableRowClassName" style="margin: .2rem 0" v-show=" type === 1 ")

         el-table-column(prop="projectId" label="" width="100" )
            template(scope="scope")
              .ds-button.text-button.blue(@click="(row = scope.row) && (show = true) && (type = 0) ") {{ scope.row.projectId }}

          el-table-column(prop="nickName" label="" width="180")
          
          el-table-column(prop="writeTime" label="" width="120" align="right")

          el-table-column(prop="lotteryName" label="" width="100" align="right")

          el-table-column(prop="methodName" label="" width="100" align="right")

          el-table-column(prop="issue" label="" width="100" align="right")

          el-table-column(prop="code" label="" align="center" width="120")

          el-table-column(prop="multiple" label="" width="50" align="right")

          el-table-column(prop="modes" label="" width="50" align="right")
            template(scope="scope")
                span {{ MODES[scope.row.modes] }}     

          el-table-column(prop="totalPrice" label="" width="80" align="right")

          el-table-column(prop="bonus" label="" width="80" align="right")

          el-table-column(prop="prizeCode" label="" width="80" align="right")

          el-table-column(label="" width="60" align="right")
            template(scope="scope")
              span(:class="{ 'text-danger': scope.row.stat === 3,  'text-grey': scope.row.stat === 0, 'text-green': scope.row.stat === 2, 'text-black': scope.row.stat === 1}") {{ STATUS[scope.row.stat] }}

      el-pagination(:total="total" v-bind:page-size="pageSize" layout="prev, pager, next, total" v-bind:page-sizes="[5, 10, 15, 20]" v-bind:current-page="currentPage" small v-if=" total > 20 " v-on:current-change="pageChanged")  

  
    .modal(v-show="show" )
      .mask
      .box-wrapper
        .box
          .tool-bar
            span.title {{ modalTitles[type] }}
            el-button-group
              el-button(icon="close" @click="show = false")
          .content
            el-row
              el-col(:span="9")
                
                span(style="color: #333") {{ row.nickName }}
              el-col(:span="5")
                
                span(style="color: #333") {{ row.lotteryName }}
              el-col(:span="5")
                
                span(style="color: #333") {{ row.prizeCode }}

              el-col(:span="5")
                
                span(style="color: #333") {{ row.totalPrice }}

            el-row
              el-col(:span="9")
                
                span(style="color: #333") {{ row.projectId }}
              el-col(:span="5")
                
                span(style="color: #333") {{ row.methodName }}
              el-col(:span="5")
                
                span(style="color: #333") {{ STATUS[row.stat] }}

              el-col(:span="5")
                
                span(style="color: #333") {{ row.multiple }}

            
            el-row
              el-col(:span="9")
                
                span(style="color: #333") {{ row.writeTime }}
              el-col(:span="5")
                
                span(style="color: #333") {{ row.issue }}
              el-col(:span="5")
                
                span(style="color: #333") {{ row.bonus }}

              el-col(:span="5")
                
                span(style="color: #333") {{ row.userPoint }}

            p.textarea-label
              span.label 
              el-input.font-12(v-model="row.code" type="textarea" autofocus  v-bind:autosize="{ minRows: 5, maxRows: 10 }" placeholder=" [ ][,]  [;] ")

            p 

            el-table.header-bold.nopadding(:data="[]" v-bind:row-class-name="tableRowClassName" style="margin: .15rem 0")

              el-table-column(prop="methodName" label="" width="160" )
                template(scope="scope")
                  .ds-button.text-button.blue(@click="") xxxx

              el-table-column(label="" width="160")
              

              el-table-column(prop="prize" label="" width="80" align="right")

              el-table-column(prop="prize" label="" width="80" align="right")

              el-table-column(prop="userpoint" label=""  align="right")

            .buttons(style="margin: .3rem; text-align: center")
              .ds-button.primary.large.bold(v-if="type === 1" @click="") 
              .ds-button.primary.large.bold(v-if="type === 2" @click="cancel") 
    
    // .modal(v-show="show" )
      .mask
      .box-wrapper
        .box
          .tool-bar
            span.title 
            el-button-group
              el-button(icon="close" @click="show = false")
          .content
            el-row
              el-col(:span="6")
                
                span(style="color: #333") {{ user.name }}
              el-col(:span="6")
                
                span(style="color: #333") {{ user.game }}
              el-col(:span="6")
                
                span(style="color: #333") {{ user.game }}

              el-col(:span="6")
                
                span(style="color: #333") {{ user.pay }}

            el-row
              el-col(:span="6")
                
                span(style="color: #333") {{ user.name }}
              el-col(:span="6")
                
                span(style="color: #333") {{ user.game }}
              el-col(:span="6")
                
                span(style="color: #333") {{ user.game }}

              el-col(:span="6")
                
                span(style="color: #333") {{ user.pay }}

            
            el-row
              el-col(:span="6")
                
                span(style="color: #333") {{ user.name }}
              el-col(:span="6")
                
                span(style="color: #333") {{ user.game }}
              el-col(:span="6")
                
                span(style="color: #333") {{ user.game }}

              el-col(:span="6")
                
                span(style="color: #333") {{ user.pay }}

            p.textarea-label
              span.label 
              el-input.font-12(v-model="value" type="textarea" autofocus  v-bind:autosize="{ minRows: 5, maxRows: 10 }" placeholder=" [ ][,]  [;] ")

            p 

            el-table.header-bold.nopadding(:data="data" v-bind:row-class-name="tableRowClassName" style="margin: .15rem 0")

              el-table-column(prop="methodName" label="" width="160" )
                template(scope="scope")
                  .ds-button.text-button.blue(@click="") xxxx

              el-table-column(label="" width="160")
              

              el-table-column(prop="prize" label="" width="80" align="right")

              el-table-column(prop="prize" label="" width="80" align="right")

              el-table-column(prop="userpoint" label=""  align="right")

            .buttons(style="margin: .3rem; text-align: center")
              .ds-button.primary.large.bold 

    
      
      
</template>

<script>
  import { digitUppercase } from '../../util/Number'
  import { dateTimeFormat } from '../../util/Date'
  import api from '../../http/api'
  import util from '../../util'
  export default {
    data () {
      return {
        st: '',
        et: '',
        STATUS: ['', '', '', ''],
        status: '',
        ISFREE: ['', ''],
        isFree: '',
        gameList: [],
        game: '',
        methodList: [],
        method: '',
        issueList: [],
        issue: '',
        MODES: ['', '', '', ''],
        mode: '',
        id: '',
        name: '',
        ZONES: ['', '', ''],
        zone: '',
        data: [{}],
        pageSize: 20,
        total: 0,
        currentPage: 1,
        preOptions: {},
        // modal
        show: false,
        type: 0,
        row: {},
        modalTitles: ['', '', '']
      }
    },
    computed: {
      textMoney () {
        return digitUppercase(this.money)
      },
      Cdata () {
        if (this.data.length <= this.pageSize) return this.data
        else {
          return util.groupArray(this.data.slice(this.pageSize * (this.currentPage - 1), this.pageSize * this.currentPage), this.pageSize, {_empty: true})[0]
        }
      }
    },
    watch: {
      game () {
        this.getMethods()
        this.getRecentIssueList()
      }
    },
    mounted () {
      this.getLotterys()
      this.Orderlist()
    },
    methods: {
      pageChanged (cp) {
        this.Orderlist(cp, () => {
          this.currentPage = cp
        })
      },
      clear () {
        this.st = ''
        this.et = ''
        this.status = ''
        this.isFree = ''
        this.game = {}
        this.method = {}
        this.issue = ''
        this.mode = ''
        this.id = ''
        this.name = ''
        this.zone = ''
      },
      cancel () {
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(api.cancel, {id: this.row.projectId}).then(({data}) => {
          // success
          if (data.success === 1) {
            setTimeout(() => {
              loading.text = '!'
            }, 100)
          } else loading.text = '!'
        }, (rep) => {
          // error
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      },
      Orderlist (page, fn) {
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        if (!fn) {
          this.preOptions = {
            beginDate: this.st ? dateTimeFormat(this.st.getTime()).replace(/[\s:-]*/g, '') : '',
            endDate: this.et ? dateTimeFormat(this.et.getTime()).replace(/[\s:-]*/g, '') : '',
            stat: this.status,
            isFree: this.isFree,
            userName: this.name,
            scope: this.zone,
            lotteryId: this.game.lotteryId,
            methodId: this.method.methodId,
            issue: this.issue,
            modes: this.mode !== '' ? this.mode + 1 : '',
            page: 1,
            pageSize: this.pageSize
          }
        } else {
          this.preOptions.page = page
        }
        this.$http.get(api.Orderlist, this.preOptions).then(({data}) => {
          // success
          if (data.success === 1) {
            setTimeout(() => {
              loading.text = '!'
            }, 100)
            typeof fn === 'function' && fn()
            this.data = data.recordList
            this.total = data.totalSize || this.data.length
          } else loading.text = '!'
        }, (rep) => {
          // error
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      },
      getLotterys () {
        this.$http.get(api.getLotterys).then(({data}) => {
          // success
          if (data.success === 1) {
            this.gameList = data.lotteryList
          }
        }, (rep) => {
          // error
        })
      },
      getMethods () {
        this.$http.get(api.getMethods, {lotteryId: this.game.lotteryId}).then(({data}) => {
          // success
          if (data.success === 1) {
            this.methodList = data.methodList
          }
        }, (rep) => {
          // error
        })
      },
      getRecentIssueList () {
        this.$http.get(api.getRecentIssueList, {lotteryId: this.game.lotteryId, issCount: 30}).then(({data}) => {
          // success
          if (data.success === 1) {
            this.issueList = data.issueList
          }
        }, (rep) => {
          // error
        })
      }
    }
  }
  // 
  // http://192.168.169.44:9901/cagamesclient/report/buyReport.do?method=list&beginDate=20170101000000&endDate=20170303000000&stat=1&isFree=1&userName=test&scope=1&lotteryId=1&methodId=16&issue=170104071&modes=1
  // Orderlist: api + 'report/buyReport.do?method=list',
  // Id
  // http://192.168.169.44:9901/cagamesclient/report/buyReport.do?method=detail&projectId=2290
  // OrderDetail: api + 'report/buyReport.do?method=list',
  // 
  // http://192.168.169.44:9901/cagamesclient/booking.do?method=cancel&id=1304
  // cancel: api + '/booking.do?method=cancel',
  // 
  // http://192.168.169.44:9901/cagamesclient/report/OrderReport.do?method=getMethods&lotteryId=1
  // getMethods: api + 'report/OrderReport.do?method=getMethods',
  // 
  // http://192.168.169.44:9901/cagamesclient/issue.do?method=getRecentIssueList&lotteryId=1&issCount=10
  // getRecentIssueList: api + 'issue.do?method=getRecentIssueList'
  // 
  // http://192.168.169.44:9901/cagamesclient/report/OrderReport.do?method=getLotterys
  // getLotterys: api + 'report/OrderReport.do?method=getLotterys',
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .scroll-content
    top TH
  .me-detail-info
    padding 0 PWX
    .base-info
      text-align center
      color #333
      padding 0 .5rem
    .el-table
      color #333
      .th .cell
        font-weight bold
  .item
    display inline-block

</style>

<style lang="stylus" scoped>

  @import '../../var.stylus'

  bg = #d8d8d8
  bg-hover = #ececec
  bg-active = #e2e2e2
  .tool-bar
    height TH
    line-height TH 
    background-color bg
    font-size .12rem
    border-top-right-radius .05rem
    border-top-left-radius .05rem
    overflow hidden
    background-position .2rem center

  .title
    color #333
    font-weight bold
    padding-left .2rem

  .el-button-group
    float right
    height 100%
    .el-button
      font-size .12rem
      color GREY
      border none
      height 100%
      width TH
      padding 0
      background-color transparent
      &:hover
        background-color bg-hover
      &:active
        background-color bg-active
      &:first-child
        font-size .16rem

  .modal 
    position absolute
    top 0
    bottom 0
    left 0
    right 0
    text-align center
    z-index 9999
    
    .mask
      position absolute
      left 0
      top 0
      width 100%
      height 100%
      opacity .5
      background #000
      z-index 9998
    .box-wrapper
      position absolute
      top 0
      bottom 0
      left 0
      right 0
      text-align center
      z-index 9999
      &:after
        content ''
        height 100%
        width 0
        vertical-align middle
        display inline-block
    .box
      position relative
      text-align left
      display inline-block
      vertical-align middle
      background-color #ededed
      font-size .12rem
      width 7rem
      radius()
    .content
      margin 0 .2rem
      .el-row
        margin PW 0
      .textarea-label
        position relative
        margin .3rem .3rem .3rem 0
        .label
          position absolute
          left 0
          top .05rem
        .el-textarea
          display inline-bock
          vertical-align top
          padding-left .6rem 
          .textarea
            font-size .12rem

</style>


