<template lang="jade">
  .me-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    MeSideView(type="safe")
    .safe-detail-info.scroll-content
      // 
      el-row.pwd(v-bind:class="{expand: index === 1 }")
        el-col
          el-row.static
            el-col(:span="6").title.ds-icon-lock 

            el-col(:span="6").login-pwd 
              span.text-green.ds-icon-set(v-if="me.pwd") 
              span.text-danger.ds-icon-unset(v-if="!me.pwd") 

            el-col(:span="8").cash-pwd 
              span.text-green.ds-icon-set(v-if="me.cashPwd") 
              span.text-danger.ds-icon-unset(v-if="!me.cashPwd") 

            el-col(:span="4").toggle
              .ds-button.text-button.blue(@click="index === 1 ? index = 0 : index = 1") {{ index === 1 ? '' : !me.pwd ? '' : '' }}

          el-row.action(v-if="index === 1")
            .ds-button-group
              .ds-button.large.text-button(v-bind:class="{selected: tabIndex === 1}" @click="tabIndex = 1") 
              .ds-button.large.text-button(v-bind:class="{selected: tabIndex === 2}" @click="tabIndex = 2") {{ !me.cashPwd ? '' : '' }}

            .pwd-form.form(v-if="tabIndex === 1")
              p &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                input.ds-input.large(v-model="oldPwd" type="password")
              p &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                input.ds-input.large(v-model="newPwd" type="password")
                span.notice 6-16;
                  br
                  
              p 
                input.ds-input.large(v-model="newPwdAgain" type="password")

              

            .cashpwd-form.form(v-if="tabIndex === 2 && !me.cashPwd")

              p &nbsp;&nbsp;&nbsp;
                input.ds-input.large(v-model="newCashPwd" type="password")
                span.notice 6-16;
                  br
                  
              p &nbsp;&nbsp;&nbsp;
                input.ds-input.large(v-model="newCashPwdAgain" type="password")



            .cashpwd-form.form(v-if="tabIndex === 2 && me.cashPwd")

              p &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                input.ds-input.large(type="password" v-model="oldCashPwd")
              p &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                input.ds-input.large(type="password"  v-model="newCashPwd")
                span.notice 6-16;
                  br
                  
              p 
                input.ds-input.large(type="password"  v-model="newCashPwdAgain")


            .buttons(style="margin-left: .85rem")
              .ds-button.primary.large(@click="changePwd") 
              .ds-button.cancel.large(@click="clearPwd") 



      // 
      el-row.nickname(v-bind:class="{expand: index === 2 }")
        el-col
          el-row.static
            el-col(:span="6").title.ds-icon-nickname 

            el-col(:span="14")
              span.text-green.ds-icon-set(v-if="me.name") 
              span.text-ellipsis(v-if="me.name" style="max-width: 70%"  v-bind:title="me.name") &nbsp;&nbsp;{{ me.name }}
              span.text-danger.ds-icon-unset(v-if="!me.name") 

            el-col(:span="4").toggle
              .ds-button.text-button.blue(@click="index === 2 ? index = 0 : index = 2") {{ index === 2 ? '' : !me.name ? '' : '' }}

          el-row.action(v-if="index === 2" style="padding-left: 0")
            
            .nickname-form.form
              div(style="text-align: center; ")
                input.ds-input.large(:placeholder="nicknamePlaceholder" v-model="nickname" maxlength="20")
                .ds-button.primary.large(style="margin-left: .15rem" @click="changNickName") 
            

      // 
      el-row.greeting(v-bind:class="{expand: index === 3 }")
         el-col
          el-row.static
            el-col(:span="6").title.ds-icon-greeting 

            el-col(:span="14")
              span.text-green.ds-icon-set(v-if="me.greeting") 
              span.text-ellipsis(v-if="me.greeting" style="max-width: 70%" v-bind:title="me.greeting")  &nbsp;&nbsp;{{ me.greeting }}
              span.text-danger.ds-icon-unset(v-if="!me.greeting") 

            el-col(:span="4").toggle
              .ds-button.text-button.blue(@click="index === 3 ? index = 0 : index = 3") {{ index === 3 ? '' : !me.greeting ? '' : '' }}

          el-row.action(v-if="index === 3" style="padding-left: 0")
            
            .nickname-form.form
              div(style="text-align: center; ")
                input.ds-input(:placeholder="greeting" style="width: 82%" v-model="greeting" maxlength="50")
                .ds-button.primary.large(style="margin-left: .15rem" @click="setGreetMsg") 
      
      // 
      el-row.phone(v-bind:class="{expand: index === 4 }")
        el-col
          el-row.static
            el-col(:span="6").title.ds-icon-phone 

            el-col(:span="14").phone
              span.text-green.ds-icon-set(v-if="me.phone") 
              span(v-if="me.phone")  {{ me.phone }}
              span.text-danger.ds-icon-unset(v-if="!me.phone") 

            el-col(:span="4").toggle
              .ds-button.text-button.blue(@click="index === 4 ? index = 0 : index = 4") {{ index === 4 ? '' : !me.phone ? '' : '' }}

          el-row.action(v-if="index === 4")
            
            .phone-form.form
              p 
                input.ds-input.large(v-model="newCashPwd" type="password")
              p(v-if="!me.phone") 
                input.ds-input.large(v-model="phone" v-bind:disabled="pt_ > 0")
                button.ds-button.secondary.outline(style="margin-left: .1rem;" @click="sendSms" v-bind:class="{ disabled: pt_ }" v-bind:disabled="pt_ > 0") 
                  span(v-if="!pt_") 
                  span(v-if="pt_" style="color: #333") {{ pt_ }} 
                    span(style="color: #999") 
              
              p(v-if="me.phone") 
                span {{ me.phone }}
                button.ds-button.secondary.outline(style="margin-left: .1rem;" @click="sendSms" v-bind:class="{ disabled: pt_ }" v-bind:disabled="pt_ > 0") 
                  span(v-if="!pt_") 
                  span(v-if="pt_" style="color: #333") {{ pt_ }} 
                    span(style="color: #999") 

              p &nbsp;&nbsp;&nbsp;
                input.ds-input.large(v-model="pc_")

            .buttons(style="margin-left: .65rem")
              button.ds-button.primary.large(@click="bindPhone" v-if="!me.phone") 
              button.ds-button.primary.large(@click="unbindPhone" v-if="me.phone") 
              .ds-button.cancel.large(@click="clearPhone") 
      // 
      el-row.email(v-bind:class="{expand: index === 5 }")
        el-col
          el-row.static
            el-col(:span="6").title.ds-icon-email 

            el-col(:span="14").email
              span.text-green.ds-icon-set(v-if="me.email") 
              span(v-if="me.email")  {{ me.email }}
              span.text-danger.ds-icon-unset(v-if="!me.email") 

            el-col(:span="4").toggle
              .ds-button.text-button.blue(@click="index === 5 ? index = 0 : index = 5") {{ index === 5 ? '' : !me.email ? '' : '' }}

          el-row.action(v-if="index === 5")
            
            .email-form.form
              p 
                input.ds-input.large(v-model="newCashPwd" type="password")
              p(v-if="!me.email") 
                input.ds-input.large(v-model="email")
                span.ds-button.secondary.outline(style="margin-left: .1rem;" @click="sendMail"  v-bind:class="{ disabled: et_ }" v-bind:disabled="et_ > 0") 
                  span(v-if="!et_") 
                  span(v-if="et_" style="color: #333") {{ et_ }} 
                    span(style="color: #999") 
              
              p(v-if="me.email") 
                span {{ me.email }}
                span.ds-button.secondary.outline(style="margin-left: .1rem;" @click="sendMail"  v-bind:class="{ disabled: et_ }" v-bind:disabled="et_ > 0") 
                  span(v-if="!et_") 
                  span(v-if="et_" style="color: #333") {{ et_ }} 
                    span(style="color: #999") 

              p &nbsp;&nbsp;&nbsp;
                input.ds-input.large(v-model="ec_")

            .buttons(style="margin-left: .65rem")
              .ds-button.primary.large(@click="bindEmail" v-if="!me.email") 
              .ds-button.primary.large(@click="unbindEmail" v-if="me.email") 
              .ds-button.cancel.large(@click="clearEmail") 
      // 
      el-row.safe-question(v-bind:class="{expand: index === 6 }")
        el-col
          el-row.static
            el-col(:span="6").title.ds-icon-safe 

            el-col(:span="14").email
              span.text-green.ds-icon-set(v-if="me.safe") 
              span.text-danger.ds-icon-unset(v-if="!me.safe") 

            el-col(:span="4").toggle
              .ds-button.text-button.blue(@click="index === 6 ? index = 0 : index = 6" v-if="!me.safe") {{ index === 6 ? '' : !me.safe ? '' : '' }}

          el-row.action(v-if="index === 6 && !me.safe")
            .safe-form.form
              p 
                el-select(placeholder="" style="width: 2.2rem" v-model="q1")
                  el-option(v-for="q1 in Q" v-bind:label="q1.title" v-bind:value="q1" v-bind:disabled="q1.disabled")
              p 
                input.ds-input.large(v-model="a1")
              p 
                el-select(placeholder="" style="width: 2.2rem" v-model="q2")
                  el-option(v-for="q2 in Q" v-bind:label="q2.title" v-bind:value="q2" v-bind:disabled="q2.disabled")
              p 
                input.ds-input.large(v-model="a2")


            .buttons(style="margin-left: .65rem")
              .ds-button.primary.large(@click="setSafeQuestion") 
              .ds-button.cancel.large(@click=clearSafeQuerstion) 
      // 
      el-row.safe-check(v-bind:class="{expand: index === 7 }")
        el-col
          el-row.static
            el-col(:span="6").title.ds-icon-safecheck 

            el-col(:span="14").safeCheck
              span.text-green.ds-icon-set(v-if="me.safeCheck") 
              span.text-danger.ds-icon-unset(v-if="!me.safeCheck") 
              span(style="font-size: .12rem") 

            el-col(:span="4").toggle
              .ds-button.text-button.blue(@click="index === 7 ? index = 0 : index = 7") {{ index === 7 ? '' : !pwd ? '' : '' }}

          el-row.action(v-if="index === 7" style="text-align: center; padding-left: 0")
            
            .safe-check-form.form(style="margin-top: .3rem")
              .ds-checkbox-label.active.disabled
                .ds-checkbox
                
              .ds-checkbox-label(@click="safeCheck === 1 ? safeCheck = 0 : safeCheck = 1" v-bind:class="{active: safeCheck === 1 }")
                .ds-checkbox
                
              .ds-checkbox-label(@click="safeCheck === 2 ? safeCheck = 0 : safeCheck = 2" v-bind:class="{active: safeCheck === 2 }")
                .ds-checkbox
                
              //.ds-checkbox-label(@click="safeCheck4 = !safeCheck4" v-bind:class="{active: safeCheck4 }")
              //  .ds-checkbox
              //  
            
            p(v-if="me.safeCheck != safeCheck " style="margin-top: .2rem") 
                input.ds-input.large(v-model="safeCheckCode")
                span.ds-button.secondary.outline(style="margin-left: .1rem;" @click="getVerifyCode"  v-bind:class="{ disabled: (this.safeCheck || this.me.safeCheck) === 1 ? et_ : pt_ }" v-bind:disabled="((this.safeCheck || this.me.safeCheck) === 1 ? et_ : pt_) > 0") 
                  span(v-if="!((this.safeCheck || this.me.safeCheck) === 1 ? et_ : pt_)") 
                  span(v-if="((this.safeCheck || this.me.safeCheck) === 1 ? et_ : pt_)" style="color: #333") {{ ((this.safeCheck || this.me.safeCheck) === 1 ? et_ : pt_) }} 
                    span(style="color: #999") 


            .buttons(style="margin: .2rem 0; padding-top: 0")
              .ds-button.primary.large(@click="setsecurityCheck" v-if="safeCheckCode") 

      
      
</template>

<script>
import MeSideView from 'components/MeSideView'
import api from '../../http/api'
import store from '../../store'
import Validate from '../../util/Validate'
import xhr from 'components/xhr'
export default {
  mixins: [xhr],
  data () {
    return {
      me: store.state.user,
      oldPwd: '',
      newPwd: '',
      newPwdAgain: '',
      oldCashPwd: '',
      newCashPwd: '',
      newCashPwdAgain: '',
      nickname: '',
      greeting: '',
      phone: '',
      email: '',
      Q: [],
      q1: null,
      q2: null,
      safeCheck: 0,
      safeCheckCode: '',

      index: 0,
      tabIndex: 1
    }
  },
  computed: {
    nicknamePlaceholder () {
      return !this.me.name ? '' : this.me.name
    },
    greetingPlaceholder () {
      return !this.me.greeting ? '' : this.me.greeting
    }
  },
  watch: {
    newPwd () {
      this.newPwd = this.newPwd.trim()
    },
    newPwdAgain () {
      this.newPwdAgain = this.newPwdAgain.trim()
    },
    newCashPwd () {
      this.newPwd = this.newPwd.trim()
    },
    newCashPwdAgain () {
      this.newPwdAgain = this.newPwdAgain.trim()
    },
    q1 () {
      this.q1.disabled = true
    },
    q2 () {
      this.q2.disabled = true
    },
    a1 () {
      this.a1 = this.a1.trim()
    },
    a2 () {
      this.a2 = this.a2.trim()
    },
    safeCheck () {
      if (!this.me.email && this.safeCheck === 1) {
        this.$message.error({target: this.$el, message: '!'})
        this.safeCheck = 0
      }
      if (!this.me.phone && this.safeCheck === 2) {
        this.$message.error({target: this.$el, message: '!'})
        this.safeCheck = 0
      }
    }
  },
  mounted () {
    this.acctSecureInfo()
    this.safeQuestionList()
  },
  methods: {
    acctSecureInfo () {
      this.$http.get(api.acctSecureInfo).then(({data}) => {
        if (data.success === 1) {
          store.actions.setUser({
            email: data.email,
            phone: data.mobile,
            greeting: data.greetMsg,
            pwd: data.isSetLoginPwd,
            cashPwd: data.isSetSecurityPwd,
            safe: data.isSetSafeQuest,
            safeCheck: data.isSetVerifytype,
            safeScore: data.accountPoint,
            location: data.location,
            lastLoginTime: data.lastLoginTime.time
          })
          this.safeCheck = data.isSetVerifytype
        }
      })
    },
    changLoginPwd () {
      this.$http.post(api.changLoginPwd, { password: this.oldPwdpwd, newPwd: this.newPwd }).then(({ data }) => {

      }, (rep) => {

      })
    },
    changePwd () {
      // 
      if (this.tabIndex === 1) {
        if (!this.oldPwd) return this.$message.warning({target: this.$el, message: ''})
        if (!this.newPwd) return this.$message.warning({target: this.$el, message: ''})
        if (!Validate.pwd(this.newPwd)) return this.$message.error({target: this.$el, message: '1:6-16;2:'})
        if (this.newPwdAgain !== this.newPwd) return this.$message.error({target: this.$el, message: ''})
        // changLoginPwd: api + 'person/accountSecur.do?method=changLoginPwd&password=123456&newPwd=000000',
        this.$http.post(api.changLoginPwd, {password: this.oldPwd, newPwd: this.newPwd}).then(({data}) => {
          if (data.success === 1) {
            this.$message.success({target: this.$el, message: ' '})
            setTimeout(() => {
              this.__setCall({fn: '__logout'})
            }, 3000)
          } else {
            this.$message.error({target: this.$el, message: ''})
            this.clearPwd()
          }
        }, (rep) => {
        })
      } else {
      // 
        // 
        if (this.me.cashPwd) {
          if (!this.oldCashPwd) return this.$message.warning({target: this.$el, message: ''})
        } else {}
        // 
        if (!this.newCashPwd) return this.$message.warning({target: this.$el, message: ''})
        if (!Validate.pwd(this.newCashPwd)) return this.$message.error({target: this.$el, message: '1:6-16;2:'})
        if (this.newPwdAgain !== this.newPwd) return this.$message.error({target: this.$el, message: ''})
        // changSecurePwd: api + 'person/accountSecur.do?method=changSecurePwd&password=123456&newPwd=000000',
        this.$http.post(api.changSecurePwd, {password: this.oldCashPwd, newPwd: this.newCashPwd}).then(({data}) => {
          if (data.success === 1) {
            let message = ' '
            if (!this.me.cashPwd) message = ' '
            this.$message.success({target: this.$el, message: message})
            if (!this.me.cashPwd) store.actions.setUser({cashPwd: true})
          } else {
            this.$message.error({target: this.$el, message: ''})
            this.clearCashPwd()
          }
        }, (rep) => {
        })
      }
    },
    clearPwd () {
      this.oldPwd = ''
      this.newPwd = ''
      this.newPwdAgain = ''
    },
    clearCashPwd () {
      this.oldCasjPwd = ''
      this.newCashPwd = ''
      this.newCashPwdAgain = ''
    },
    changNickName () {
      if (!Validate.nickName(this.nickname)) return this.$message.error({target: this.$el, message: '284QQ'})
      this.$http.post(api.changNickName, {nickName: this.nickname}).then(({data}) => {
        if (data.success === 1) {
          let message = ' '
          if (!this.me.name) message = ' '
          this.$message.success({target: this.$el, message: message})
          store.actions.setUser({name: this.nickname})
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(rep => {
      })
    },
    setGreetMsg () {
      this.$http.post(api.setGreetMsg, {greetMsg: this.greeting}).then(({data}) => {
        if (data.success === 1) {
          let message = ' '
          if (!this.me.greeting) message = ' '
          this.$message.success({target: this.$el, message: message})
          store.actions.setUser({greeting: this.greeting})
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(rep => {
      })
    },
    sendSms () {
      let bind = !this.me.phone
      if (bind && !Validate.phone(this.phone)) return this.$message.error({target: this.$el, message: '11'})

      this.$http.post(api.person_sendSms, bind ? {mobile: this.phone} : {}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ' '})
          this.pt_ = this.time_
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(rep => {
      })
    },
    bindPhone () {
      if (!this.newCashPwd || !this.phone || !this.pc_) return this.$message.error({target: this.$el, message: ''})
      // this.$message.success({target: this.$el, message: ' '})
      this.$http.post(api.bindMobil, {mobile: this.phone, password: this.newCashPwd, verifyCode: this.pc_, type: 0}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ' '})
          store.actions.setUser({phone: this.phone})
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
        this.clearPhone()
      }).catch(rep => {
      })
    },
    unbindPhone () {
      if (!this.newCashPwd || !this.pc_) return this.$message.error({target: this.$el, message: ''})
      // this.$message.success({target: this.$el, message: ' '})
      this.$http.post(api.bindMobil, {password: this.newCashPwd, verifyCode: this.pc_, type: 1}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ' '})
          store.actions.setUser({phone: ''})
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
        this.clearPhone()
      }).catch(rep => {
      })
    },
    clearPhone () {
      this.newCashPwd = ''
      this.phone = ''
      this.pc_ = ''
      this.pt_ = 0
    },
    sendMail () {
      let bind = !this.me.email
      if (bind && !Validate.email(this.email)) return this.$message.error({target: this.$el, message: ''})
      this.$http.post(api.person_sendMail, bind ? {email: this.email} : {}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ' '})
          this.pt_ = this.time_
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(rep => {
      })
    },
    bindEmail () {
      if (!this.newCashPwd || !this.email || !this.ec_) return this.$message.error({target: this.$el, message: ''})
      // this.$message.success({target: this.$el, message: ' '})
      this.$http.post(api.bindEmail, {email: this.email, password: this.newCashPwd, verifyCode: this.ec_, type: 0}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ' '})
          store.actions.setUser({email: this.email})
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
        this.clearEmail()
      }).catch(rep => {
      })
    },
    unbindEmail () {
      if (!this.newCashPwd || !this.ec_) return this.$message.error({target: this.$el, message: ''})
      // this.$message.success({target: this.$el, message: ' '})
      this.$http.post(api.bindEmail, {password: this.newCashPwd, verifyCode: this.ec_, type: 1}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ' '})
          store.actions.setUser({email: ''})
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
        this.clearEmail()
      }).catch(rep => {
      })
    },
    clearEmail () {
      this.newCashPwd = ''
      this.email = ''
      this.ec_ = ''
      this.et_ = 0
    },
    safeQuestionList () {
      this.$http.get(api.safeQuestionList).then(({data}) => {
        if (data.success === 1) {
          this.Q = data.allQuestions.filter(q => {
            q.disabled = false
            return true
          })
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
      }).catch(rep => {
      })
    },
    setSafeQuestion () {
      if (!this.q1 || !this.q2 || !this.a1 || this.a2) return this.$message.error({target: this.$el, message: ''})
      // this.$message.success({target: this.$el, message: ' '})
      this.$http.post(api.setSafeQuestion, {question1: this.q1.title, question2: this.q2.title, answer1: this.a1, answer2: this.a2}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ' '})
          store.actions.setUser({safe: true})
        } else {
          this.$message.error({target: this.$el, message: data.msg || ''})
        }
        this.clearSafeQuestion()
      }).catch(rep => {
      })
    },
    clearSafeQuestion () {
      this.q1 = null
      this.q2 = null
      this.a1 = ''
      this.a2 = ''
    },
    getVerifyCode () {
      switch (this.safeCheck || this.me.safeCheck) {
        case 1:
          this.sendMail()
          break
        case 2:
          this.sendSms()
      }
    },
    setsecurityCheck () {
      // if (!this.safeCheckCode) return this.$message.warning({target: this.$el, message: ''})
      this.$http.post(api.setsecurityCheck, {checkType: this.safeCheck, verifyCode: this.safeCheckCode}).then(({data}) => {
        if (data.success === 1) {
          this.$message.success({target: this.$el, message: ' /'})
          store.actions.setUser({safeCheck: this.safeCheck})
        } else {
          this.$message.error({target: this.$el, message: data.msg || '/'})
        }
      }).catch(rep => {
      }).finally(rep => {
        this.safeCheckCode = ''
      })
    }
  },
  // 
