<script>
import common from './common'
export default {
  mixins: [common],
  data () {
    return {
      gameType: 'SSL',
      menus: [
        {
          title: '',
          groups: [
            {
              title: '',
              items: [
                {id: '+3-1-1', title: '', description: '33', example: '345 345'},
                {id: '+3-1-2', title: '', description: '3', example: '345 345'},
                {id: '+3-1-3', title: '', description: '', example: '1001,010,100,'}
              ]
            },
            {
              title: '',
              items: [
                {id: '+3-2-1', title: '', description: '0-92 ', example: '5,8,81528 ()'},
                {id: '+3-2-2', title: '', description: '3', example: '5,8,81528 ()'},
                {id: '+3-2-3', title: '', description: '0-93', example: '2,5,8121518 ()'},
                {id: '+3-2-4', title: '', description: '3', example: '2,5,8121518 ()'},
                {id: '+3-2-5', title: '', description: '3()', example: '(0,0,1),(1,2,3)(1)0,0,1(2)1,2,3'},
                {id: '+3-3-1', title: '()', description: '()', example: '3(1)003(2)012'}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              title: '',
              items: [
                {id: '2-1-1', title: '', description: '22 ', example: '5858'},
                {id: '2-1-2', title: '', description: '2', example: '5858'}
              ]
            },
            {
              title: '',
              items: [
                {id: '2-2-1', title: '', description: '0-92 ', example: '5,885 58()'},
                {id: '2-2-2', title: '', description: '2', example: '5,885 58()'}
              ]
            },
            {
              title: '',
              items: [
                {id: '2-3-1', title: '', description: '22 ', example: '5858'},
                {id: '2-3-2', title: '', description: '2', example: '5858'}
              ]
            },
            {
              title: '',
              items: [
                {id: '2-4-1', title: '', description: '0-92 ', example: '5,885 58()'},
                {id: '2-4-2', title: '', description: '2', example: '5,885 58()'}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              title: '',
              items: [
                {id: '1-1-1', title: '', description: '1', example: ' 11'}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              title: '',
              items: [
                {id: '0-1-1', title: '', description: '0-911', example: '111'},
                {id: '0-1-2', title: '', description: '0-9222', example: '1,2121'}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              title: '',
              items: [
                {id: '-2-1-1', title: '', description: '56789012341357902468', example: ''},
                {id: '-2-1-2', title: '', description: '56789012341357902468', example: ''}
              ]
            }
          ]
        }
      ],
      getTitle () {
        switch (this.type.id.slice(0, 2)) {
          case '5-':
            return ''
          case '4-':
            return ''
          case '3-':
            return ''
          case '-3':
            return ''
          case '+3':
            return ''
          case '2-':
            return ''
          case '1-':
            return ''
          case '0-':
            return ''
          case '-1':
            return ''
          case '-2':
            return ''
        }
      },
      // 
      type: {
        id: '+3-1-1',
        // 
        title: '',
        // 
        description: ''
      }
    }
  }
}
</script>



