<template lang="jade">
  .group-page(style="width: 5.4rem; height: 6.8rem")
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .contract.scroll-content(:class="{ center: type === 0 }")

      div(v-if="contracts.length === 0" style="height: 100%;") 
        div(style="width: 0; height: 100%; vertical-align: middle; display: inline-block")
      
      div.c(v-for=" (c, ci) in contracts "  v-bind:class=" ['ds-icon-stock-' + STATUS[c.stat].class ] ")
        
        h2(style="color: #333; margin: .3rem 0") {{ contracts.length > 1 ?  ci === 0 ? '' : '' : ''  }}
        p.item &nbsp;&nbsp;&nbsp;{{ c.nickName }}
        p.item {{ STATUS[c.stat].title }}
        p.item {{ c.beginTm }}   {{ c.expireTm }} 
        p.item {{ TIME[c.shareCycle] }}

        p.item(v-for="(l, i) in  c.bonusRules ") {{ RULES[i] }} &nbsp;&nbsp;&nbsp;{{ TYPE[l.ruletype].title }}
          span.text-danger {{ l.sales / 10000 }}
          | 
          span.text-danger {{ l.bounsRate }}%

        .item.buttons(style="margin: .3rem 0" v-if=" self && STATUS[c.stat].title === '' ")
          .ds-button.primary.large.bold(@click="checkContract(c.id, 1)") 
          .ds-button.cancel.large.bold(@click="checkContract(c.id, 0)") 
      
</template>

<script>
  import api from '../../http/api'
  export default {
    data () {
      return {
        // 
        self: true,
        STATUS: [
          {id: 0, title: '', class: 'wait'},
          {id: 1, title: '', class: 'done'},
          {id: 2, title: '', reason: ''},
          {id: 3, title: '', reason: ''},
          {id: 4, title: '', reason: ''}
        ],
        contracts: [],
        // 
        TIME: ['', '', ''],
        // 
        TYPE: [
          {id: 1, title: ''},
          {id: 2, title: ''}
        ],
        RULES: ['', '', '', '', '', '', '', '', '', '']
      }
    },
    watch: {
      // 
      '$route': 'openRoute'
    },
    mounted () {
      this.self = this.$route.query.self
      this.qryContractById(this.$route.query.id || '')
    },
    methods: {
      openRoute ({query: {id, self}}) {
        this.self = self
        if (id) this.qryContractById(id)
      },
      contract () {
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(api.queryMyContract).then(({data}) => {
          // success
          if (data.success === 1) {
            this.contracts = data.contractList.sort(c => {
              return c.stat < 4 && c.stat > 1
            })
            setTimeout(() => {
              loading.text = '!'
            }, 100)
          } else loading.text = '!'
        }, (rep) => {
          // error
          this.$message.error('')
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      },
      // http://192.168.169.44:9901/cagamesclient/team/contractBonus.do?method=qryContractById&contractId=12
      qryContractById (id) {
        // ID 
        if (!id || this.self) {
          this.self = true
          return this.contract()
        }
        this.$http.get(api.qryContractById, {
          contractId: id
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.contracts = data.contractList ? data.contractList : [data]
          } else this.$message(data.msg || '!')
        }, (rep) => {
          // error
          this.$message.error('')
        })
      },
      // http://192.168.169.44:9901/cagamesclient/team/contractBonus.do?method=checkContract&contractId=11&status=1
      checkContract (id, i) {
        this.$http.get(api.checkContract, {
          contractId: id,
          status: i
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.qryContractById()
          } else this.$message(data.msg || '/!')
        }, (rep) => {
          // error
          this.$message.error('/')
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .contract
    top TH
    background #fff
    text-align center
    &.center:after
      content ''
      height 100%
      width 0
      vertical-align middle
      display inline-block
    .form
      padding PWX
    .c
      margin: .3rem
      &:not(:first-child)
        margin-top .6rem
        opacity .5

  .item
    margin .24rem 0
    text-align left
    padding-left 20%
 
</style>


